import React from 'react'
import TigerDrinkingWater from '../assets/tiger-drinking-water.jpg';
import TourMyHabitToDoSo from '../assets/tour-my-habit-to-do-so.jpg';

function TigersTour() {
  return (
     <div className='tours'>
      <div className="inner-banner">
        <img className="w-100" src={TigerDrinkingWater} alt="Tiger Drinking Water" />
        <h1 className='h2 text-center'>Tiger Conservation Tour</h1>
      </div>
      <section>
        <div className='container'>
          <div className='row'>
            <div className='col-lg-6 col-12'>
              <p>The year 2023-24 marked the 50th anniversary of Project Tiger. Let us get enthralled with success of this most remarkable conservation programmes across the globe – visiting Tiger Reserves in India.</p>
              <p>The 1960s saw tigers in deep crisis – hunting all over India was rampant and their recovery rate plummeted. The 1969 IUCN (International Union for Conservation of Nature) raised alerts and in 1970 India imposed a total ban on hunting tigers. It marked a turning point.</p>
              <p>Project Tiger’s formal launch was in 1973, at nine especially designated forests in the country. </p>
              <p>Prime amongst these is the base for our trip: Ranthambhore, which has gained a reputation as a vital breeding ground. Opening with a dozen tigers, it has survived the challenges of its first decades in which its size has grown from 400 sq. km to 1,200 sq. km. Currently its tiger population is estimated at seventy.</p>
              <p>Three days of jeep safaris, accompanied by trained and local naturalists to allow you good sightings, will take you up close and personal with the tigers: well, as close as it’s feasible to be with these fierce, magnificent creatures. You’ll become intimate with their habitat.</p>
              <p>The tour extends into a bird watching excursion in India’s top birding site, Keoladeo National Park, a UNESCO and Ramsar Site, a renowned heronry and wintering ground for migratory birds, using a bicycle rickshaw and assisted by an expert bird guide.</p>
              <p>After these top Indian wildlife sites, we end the tour with a visit to perhaps the most fabled tourist destination in the world – the Taj Mahal– before your return flight to Delhi.</p>


              <h3 className='h4 mt-5 mb-3'><i class="fas fa-hand-point-down"></i> Itinerary </h3>
              <div className='itinerary-card mt-3'>
                <p><strong>01 Day. New Delhi – Sawai Madhopur (Ranthambhore Tiger Reserve). </strong>Welcome on arrival at airport. Proceed to railway station (40 min) to board connecting a/c train. Reach (5 hrs journey) Sawai Madhopur. Transfer (10 min) to Tiger Den Resort, located in the wilderness. This is nature at its best, with all the guest cottages spread out separately. Overnight.</p>
              </div>
              <div className='itinerary-card mt-3'>
                <p><strong>02 – 03 – 04 Days. Sawai Madhopur (Ranthambhore Tiger Reserve). </strong>We will have morning (6.30 to 10 am) and afternoon (2.30 to 6 pm) jeep safaris each day with a knowledgeable nature guide and versatile driver. We will adopt different routes to take in information from fresh reports of tiger sighting. Thrill at watching Sambhar, Spotted deer, Blue Bull, Wild Boar, Mongoose, Marsh Crocodile, amidst a flutter of some 100 bird species, Owls out in the day, Indian Thick-knee by the edge of water and peacocks all around. And wait for the Tiger or Leopard or Sloth Bear to come face to face with you. Raj Bag, Padam Talab, Malik Talab, Lakarda, Bherda, Jhalra, Tamba Khan, Naal Ghati, Phuta Khurra are the most promising habitats where sightings are prominent. An ancient fort stands amidst this forest where the first self-sacrifice by Rajput ladies took place in 12th century. These are easy-paced safaris but we need to cover ours faces sometimes to avoid dirt raised by moving jeeps. For morning safaris we like to carry blankets from rooms to save ourselves from extreme cold and fog. A woollen cap is recommended. Overnight.</p>
              </div>
              <div className='itinerary-card mt-3'>
                <p><strong>05 - 06 Days. Sawai Madhopur – Bharatpur. </strong>Transfer (7.30am) to station to board a/c train. Reach Bharatpur by 9 am. Transfer to Lakshmi Vilas heritage resort. We will have day long bird watching excursion in Keoladeo National Park, a UNESCO and Ramsar Site, using a bicycle rickshaw and assisted by an expert bird guide, on both days. No restrictions here on walking. It is a renowned heronry and wintering ground for migratory birds. We will target Owls, Eagles, Nightjars, Bittern, Crake, among others. Birds on either side of walking track for a few miles and we are free to walk all around. Great photography opportunities. We will explore grassland habitats to observe Harriers, Falcons, Larks, Pipits, among other birds.</p>
              </div>
              <div className='itinerary-card mt-3'>
                <p><strong>07 Day. Bharatpur – Agra. </strong>We like to spend forenoon (drive) in areas outside the Keoladeo Park to observe new birds like Great Thick-knee, Indian Thick-knee, Yellow-wattled Lapwing, Thrushes, Bats, Indian Coursers and maybe Indian Skimmer. After lunch, we drive (1.5 hrs) and reach Agra to stay at nice hotel. Spend sun set time at the fabulous Taj Mahal. Noisy Parakeet, Common Myna, Crimson Sunbird, Laughing Dove, Black Kite, and other garden birds will be around us as we walk across this rectangular garden complex in typical Mughal design. Common Moorhen, Little Ringed Plover, Ruff, Common Sandpiper, Black Ibis, Herons (Grey, Pond and Black-capped Night), Egrets (Cattle, Intermediate and Little), Black-winged Stilts and some ducks will be found at the Yamuna river that flows over northern parts of this monument. Overnight.</p>
              </div>
              <div className='itinerary-card mt-3'>
                <p><strong>08 Day. Agra – Delhi. </strong>Leisurely breakfast at this comfortable hotel. After lunch check out and visit Red Fort where almost all Mughal emperors lived. Drive (4 hrs) or take a train to reach Delhi in evening. Transfer to airport to board flight. Farewell.</p>
              </div>
              
            <div className='itinerary-card mt-3'>
              <p><strong>Price to include:</strong>All accommodation, all meals (B,L,D), all transfers/transport, Naturalist (5 days) entry fees, jeep safaris, workshop, train fare (A/c: New Delhi – Sawai Madhopur; Sawai Madhopur – Bharatpur), excursion in villages, and assistance throughout. The itinerary can be changed as per suggestions. No fixed departure and start on own dates. Welcome to extend the Tour and let us know what other species you will like to observe. Single or couple or more in a group will be welcome. October 2022 to June 2023 will be good times to have this tour.</p>
            </div>
            <p className='mt-4'><strong>Note: The itinerary can be modified as per suggestions.</strong></p>
            </div>
            <div className='col-lg-6 col-12 ps-lg-5 ps-0'>
              <img className="w-100 rounded-xl sticky-t-20" src={TourMyHabitToDoSo} alt="Tour My Habit To Do So" />
            </div>
          </div>
         </div>
       </section >
    </div>
  )
}

export default TigersTour