import React from 'react'
import WesternGhatsBirdTourbanner from '../assets/western-ghats-bird-tour.jpg';
import GreyFrontedGreenPigeoneBird from '../assets/grey-fronted-green-pigeon-eBird.jpg';

function WesternGhatsBirdTour() {
  return (
    <div className='tours'>
      <div className="inner-banner">
        <img className="w-100" src={WesternGhatsBirdTourbanner} alt="Western Ghats Bird Tour" />
        <h1 className='h2 text-center'>Western Ghats Birding Tour</h1>
      </div>
      <section>
        <div className='container'>
          <div className='row'>
            <div className='col-lg-6 col-12'>
              <p>The Western Ghats are internationally recognized as a region of immense global importance for the conservation of biological diversity, besides containing areas of high geological, cultural and aesthetic values. A chain of mountains running parallel to India’s western coast, approximately 30-50 km inland, the Ghats traverse the States of Kerala, Tamil Nadu, Karnataka, Goa, Maharashtra and Gujarat. They cover an area of 62,000 sq., miles. It is a UNESCO World Heritage Site. This mountain chain is recognized as one of the world’s eight ‘hottest hotspots’ of biological diversity along with Sri Lanka. The forests of the Western Ghats include some of the best representatives of non-equatorial tropical evergreen forests in the world.</p>
              <p><strong>Global concern: </strong>At least 325 globally threatened species occur in the Western Ghats. The globally threatened flora and fauna in the Western Ghats are represented by 229 plant species, 31 mammal species, 15 bird species, 43 amphibian species, 5 reptile species and 1 fish species. Of these, 129 are classified as Vulnerable, 145 as Endangered and 51 as Critically Endangered. It has maximum number of bird species endemic to the Western Ghats.</p>
              <p>The Western Ghats are home to some 17 percent of the world’s Tigers and about 30 percent of the world’s Asian Elephants. Approximately 245 million people live in the peninsular Indian states that receive most of their water supply from rivers originating in the Western Ghats. This tour is especially designed to offer rare and endemic bird species of the southern Western Ghats by visiting Periyar, Munnar, Valparai and Thattekad, areas falling in states of Kerala and Tamil Nadu in India.</p>
              
              <h3 className='h4 mt-5 mb-3'><i class="fas fa-hand-point-down"></i> Itinerary </h3>
              <div className='itinerary-card mt-3'>
                <p><strong>01 Day. Kochi. </strong>An executive meets us at the airport. Transfer to hotel. Overnight.</p>
              </div>
              <div className='itinerary-card mt-3'>
                <p><strong>02 – 03 – 04 Days. Kochi/Periyar. </strong>Drive up hill (130 km, about 5 hrs). Bird watching in Periyar in the afternoon. Three overnights at a resort located amidst verdant green hills.  Periyar is the focal zone for bird specialties of the Western Ghats, as well as resident hill species and migrant birds. Potential birds include: Velvet-fronted Nuthatch, Bronzed Drongo, Greater Racket-tailed Drongo, Orange Minivet, Great Tit, Jungle Babbler, Rufous Treepie, Yellow-browed Bulbul, Malabar or Common Woodshrike, Ashy Drongo, Small Minivet, Common, Black-rumped, White-naped and Greater Flameback Woodpecker, White-bellied Treepie, Pale-billed, Nilgiri and Thick-billed Flowerpecker, Indian Golden, Black-hooded and Black-naped Oriole, Asian Fairy-bluebird, Gold-fronted Leafbird, Coppersmith, Malabar, White-cheeked and Brown-headed Barbet, various Phylloscopus (Leaf) Warblers and Flycatchers, Brown-cheeked Fulvetta, etc.</p>
              </div>
              <div className='itinerary-card mt-3'>
                <p><strong>05 – 06 – 07 Days. Periyar/Munnar. </strong>Drive (140 km, about 3 hrs). We reach Munnar in afternoon. Munnar is a hill station in Kerala and is known for some endemic species in the Western Ghats. Six endemics can be seen around Munnar. The shola-grassland eco-system is typical of Munnar. Potential species include: Nilgiri Woodpigeon, Nilgiri Pipit, Grey-headed Bulbul, Black-and-orange Flycatcher, Indian Broad-tailed Grass-warbler, Small Sunbird, Grey-breasted (now Kerala) Laughing Thrush, Nilgiri Flycatcher, Indian Rufous Babbler, White-bellied Blue Flycatcher, White-bellied Treepie, Wyanad Laughing Thrush, etc. Three overnights at a resort located amidst slopes.</p>
              </div>
              <div className='itinerary-card mt-3'>
                <p><strong>08 – 09 — 10 Days. Munnar/Valparai. </strong>Drive (about 5 hrs) through Chinnar Sanctuary, stopping at places when birds are spotted, be careful I ast is Elephant corridor. It is located in the rain shadow region of the Western Ghats and is home to the Grizzled Giant Squirrel. Birds expected here include: White-naped Flameback, Jerdon’s Baza, Large Grey Babbler, White-bellied Woodpecker, Rain Quail, Blue-breasted Quail, White-rumped Shama, Streak-throated Swallow, Kerala Laughing Thrush, Speckled Piculet, Blue-bearded Bee-eater, Sirkeer Malkoha, Indian and Oriental Scops Owls, Indian Eagle-owl, Black Baza etc. Three overnight at a tea estate bungalow where birds are all around.</p>
                <p>The area, enveloped by undulating tea plantations, is a fascinating place in Western Ghats. It is home to Liontail Macaque, Bonnet Macaque, Common Langur, Nilgiri Langur, Malabar Giant Squirrel (or Indian Giant Squirrel) and Grizzled Indian Squirrel, as well as Tiger, Gaur, Pangolin, Sambar, Spotted Deer, Barking Deer, Mouse Deer, Wild Boar, Sloth Bear, Porcupine, and Nilgiri Tahr.</p>
              </div>
              <div className='itinerary-card mt-3'>
                <p><strong>11 – 12 – 13 Days. Valparai/Thattekkad. </strong>Drive (about 6 hrs) and reach Thattekad, the ideal bird watching zone in the Western Ghats. Stay in a tent camp. Thattekad Bird Sanctuary is also known as Dr Salim Ali Bird Sanctuary and is the richest bird habitat in peninsular India. Much of the forest has been converted to agricultural and plantation purposes, but the surviving habitat offers great bird diversity with more than 270 species in a relatively small area of 25 sq., km. Nine endemics are found here including the rare Mottled Wood Owl, Forest Eagle-owl, Malayan Night-heron, Ceylon Frogmouth, Rusty-tailed Flycatcher, Grey-headed Bulbul and Nilgiri Woodpigeon. Three overnights at a river side resort.</p>
              </div>
              <div className='itinerary-card mt-3'>
              <p><strong>14 Day. Thattekad/Angamali Airport. </strong>Drive (45 km, about 1.5 hrs) to reach Angamali, the international airport for Cochin, to board flight back home. Farewell.</p>
            </div>
            <div className='itinerary-card mt-3'>
              <p><strong>Price to include:</strong>All accommodation, all meals (B,L,D), all transfers/transport, Naturalist (12 days), entry fees, and assistance throughout.</p>
            </div>
            <p className='mt-4'><strong>Note: The itinerary can be changed as per suggestions. No fixed departure and start on own dates. Welcome to extend the Tour and let us know what other species will you like to observe.</strong></p>
            </div>
            <div className='col-lg-6 col-12 ps-lg-5 ps-0'>
              <img className="w-100 rounded-xl sticky-t-20" src={GreyFrontedGreenPigeoneBird} alt="Grey Fronted Green Pigeon eBird" />
            </div>
          </div>
         </div>
       </section >
    </div>
  )
}

export default WesternGhatsBirdTour